import logo from './logo.svg';
import './App.css';
import Experiment from './Experiment';
import Diary from './Diary';
import Snackbar from '@mui/material/Snackbar';
import {useState, useEffect, useRef} from 'react';
import { styled } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import Fab from '@mui/material/Fab';
import { indigo, pink } from '@mui/material/colors';
import MenuIcon from '@mui/icons-material/Menu';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import AddIcon from '@mui/icons-material/Add';
import CheckIcon from '@mui/icons-material/Check';
import CopyAll from '@mui/icons-material/CopyAll';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PropTypes from 'prop-types';
import CssBaseline from '@mui/material/CssBaseline';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import Container from '@mui/material/Container';
import Slide from '@mui/material/Slide';
function HideOnScroll(props) {
  const { children, window } = props;
  
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    window: PropTypes.func,
  });

  return (
    !trigger?<Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>:<></>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
};

const outerTheme = createTheme({
  palette: {
    primary: {
      main: indigo[500],
    },
    secondary: {
      main: pink[400],
    },
  },
});
const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  alignItems: 'flex-start',
  paddingTop: theme.spacing(8),

  paddingBottom: theme.spacing(8),
  // Override media queries injected by theme.mixins.toolbar
}));

function App() {
  const [value, setValue] = useState('1');
  const [lock, setLock] = useState(false);
  const [text, setText] = useState()
  const [open, setOpen] = useState(false)
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const [scrollTop, setScrollTop] = useState(0)
  const [trigger, setTrigger] = useState(false)
  const[clicked, setClicked] = useState(false)
  const init = () => {
    const onScroll = e => {
      if(lock) return;
      setScrollTop(e.target.documentElement.scrollTop);
      if(e.target.documentElement.scrollTop > 100) {
        setTrigger(true);
        setLock(true)
        document.documentElement.style.overflow = 'hidden'
        setTimeout(() => document.documentElement.scroll(0, 0), 10)
        setTimeout(() => document.documentElement.style.overflow = 'scroll', 1000)
      };
    };
    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }
  const appbar = useRef()
  useEffect(init, [scrollTop]);

  return (
    <div className="App">
      <ThemeProvider theme={outerTheme}>
      

      <Slide in={!trigger}>
        <AppBar position="static" color="primary" >
        <StyledToolbar style={{justifyContent: 'center'}}>
          
        <Stack sx={{ alignItems: "center", minHeight: trigger?0:'50vh', flexDirection: 'column', width: '100%', flexGrow: 2, paddingTop: 6}}  style={{ justifyContent: 'center'}}>
        <Typography variant="h3" component="div" sx={{ fontWeight: 500, }}>
            
          </Typography>
          <Typography variant="h6" component="div" sx={{ alignText: 'center', marginTop: 4, opacity: 0.8, flexGrow: 4}}>
            
          </Typography>
          <List sx={{alignSelf:'flex-start', flexGrow: 2}}>
            <ListItem >
              <ListItemIcon>
                <CheckIcon sx={{color: 'white',}}/>
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={null}
                    
                  />
                </ListItem>
                <ListItem>
              <ListItemIcon>
                <CheckIcon sx={{color: 'white',}}/>
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={null}
                  />
                </ListItem>
                <ListItem>
              <ListItemIcon>
                <CheckIcon sx={{color: 'white',}}/>
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={null}
                  />
                </ListItem>
          </List>
          
          <CssBaseline />
          </Stack>
        </StyledToolbar>
      </AppBar>
      </Slide>
      <Slide in={trigger}><AppBar position="fixed" color="primary"  >
        <Toolbar sx={{  justifyContent: "space-between" }} ref={appbar}>
          
        <Typography variant="h6" component="div" noWrap >
        
          </Typography>
          <CssBaseline />
          <IconButton
            size="large"
            aria-label="display more actions"
            edge="end"
            color="inherit"
            onClick={(e) => {navigator.clipboard.writeText(text); setOpen(true  )}}
          >
            <CopyAll />
          </IconButton>
        </Toolbar>
      </AppBar ></Slide>
      
      
      
      <Box sx={{ width: '100%', typography: 'body1', marginTop: !trigger?'0' : (-136) - 208 - Number(appbar.current.offsetHeight) +'px'}}>
      <TabContext value={value}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList onChange={handleChange} aria-label="lab API tabs example">
            <Tab label="" value="1" />
            <Tab label="" value="2" />
          </TabList>
        </Box>
        {console.log(appbar.current)}
        <TabPanel value="1">
          <Experiment clicked={clicked} text={text} setText={setText}/>
        </TabPanel>
        <TabPanel value="2">
          <Diary clicked={clicked} text={text} setText={setText}/>
        </TabPanel>
      </TabContext>
      </Box>
      
      <Container>
        
      </Container>
      <Slide in={trigger} direction='left'>
      <Fab color="secondary" style={{
  position: 'fixed',
  bottom: 16,
  right: 16,
}} onClick={() => setClicked(!clicked)}>
        <PlayArrowIcon />
      </Fab>
      </Slide>
      <Snackbar
  open={open}
  autoHideDuration={2000}
  onClose={() => setOpen(false)}
  message="" 
/>
      </ThemeProvider>
    </div>
  );
}

export default App;
