import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import {useState, useEffect, useRef} from 'react';
const data = require('./Experiment.json');
function generateExperiment(o1, o2, o3, o4, o5, o6){
    console.log('hi')
    var template = JSON.parse(JSON.stringify(data));
    let l = [], y;
    template.forEach(e=>'string'==typeof e[0]?l.push(e):void e[Math.floor(e.length*Math.random())].forEach(e=>l.push(e)));
    l=l.map(e=>e[Math.floor(e.length*Math.random())]).join('');
    y=l.replaceAll('term1',o1.current.querySelector('input').value)
    .replaceAll('term2',o2.current.querySelector('input').value)
    .replaceAll('subject',o3.current.querySelector('input').value)
    .replaceAll('issue1',o4.current.querySelector('input').value)
    .replaceAll('issue2',o5.current.querySelector('input').value)
    .replaceAll('highlight',o6.current.querySelector('input').value);
    for(;y.includes('randomTerm');)y=y.replace('randomTerm',.5<Math.random()?o1.current.querySelector('input').value:o2.current.querySelector('input').value);
    for(;y.includes('randomIssue');)y=y.replace('randomIssue',.5<Math.random()?o4.current.querySelector('input').value:o5.current.querySelector('input').value);
    return y
}

function Experiment({clicked, text, setText}){
    
    const o1 = useRef(), o2 = useRef(), o3 = useRef(),o4 = useRef(), o5 = useRef(), o6 = useRef()
    useEffect(() => setText(generateExperiment(o1, o2, o3, o4, o5, o6)), [clicked])
    return (<>
    
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" sx={{width: '100%'}} ref={o3}/>
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" ref={o1} sx={{width: '100%', marginTop: 2}}/>
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" ref={o2} sx={{width: '100%', marginTop: 2}}/>
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" ref={o4} sx={{width: '100%', marginTop: 2}}/>
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" ref={o5} sx={{width: '100%', marginTop: 2}}/>
    <TextField id="outlined-basic" label="" variant="outlined" defaultValue="" ref={o6} sx={{width: '100%', marginTop: 2}}/>
    <Typography variant="body" component="div" sx={{mt: 2, mb: 8}} id="text">
        {text}
    </Typography>
    </>);
}
export default Experiment